import React from 'react'
import './Header.scss'
import { Navbar } from '..'
import { Apple, Shop } from '@mui/icons-material';
import sideimg from '../../images/slider-dec.png'
import { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import { useState } from 'react';

const Header = () => {


  





  useEffect(() => {
    AOS.init({ duration: 500 });
  }, []);

  return (
    <div className="header" id="header">
      <div className="inner_header">
        <Navbar />

        <div className="home_container">
          <div
            className="home_content"
            data-aos="slide-right"
            data-aos-duration="1000"
          >
            <h2>Get The Latest App From App Stores</h2>
            <p>
              Chain App Dev is an app landing page HTML5 template based on
              Bootstrap v5.1.3 CSS layout provided by TemplateMo, a great
              website to download free CSS templates.
            </p>
            <div className="homepage_button">
              <a href="">
                Free Quote <Apple className="home_btn_icons" />{" "}
              </a>
              <a href="">
                Free Quote <Shop className="home_btn_icons" />{" "}
              </a>
            </div>
          </div>
          <div className="home_image">
            <img
              src={sideimg}
              // className="fadeInRight"
              alt=""
              data-aos="slide-left"
              data-wow-duration="1s"
              data-aos-duration="1000"
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header